/*=====================
    1.General CSS start
    ==========================*/

html {
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  font-family: $font_0, $font_1;
  position: relative;
  background: $white;
  font-size: 14px;
  overflow-x: hidden;
  min-width: unset !important;
}

h1 {
  font-size: 60px;
  color: $dark-font;
  font-weight: 700;
  text-transform: uppercase;
  span {
    font-size: 107px;
    font-weight: 700;
    color: var(--theme-deafult);
  }
}

h2 {
  font-size: 36px;
  color: $dark-font;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.02em;
}

h3 {
  font-size: 24px;
  font-weight: 400;
  color: $grey;
  letter-spacing: 0.03em;
}

h4 {
  font-size: 18px;
  text-transform: capitalize;
  font-weight: 400;
  letter-spacing: 0.03em;
  line-height: 1;
}

h5 {
  font-size: 16px;
  font-weight: 400;
  color: $dark-font;
  line-height: 24px;
  letter-spacing: 0.05em;
}

h6 {
  font-size: 14px;
  font-weight: 400;
  color: $grey;
  line-height: 24px;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
}

li {
  display: inline-block;
}

p {
  font-size: 14px;
  color: $grey;
  line-height: 24px;
  letter-spacing: 0.03em;
}

a {
  transition: 0.5s ease;
  text-decoration: none;
  &:hover {
    text-decoration: none;
    transition: 0.5s ease;
  }
  &:focus {
    outline: none;
  }
}

button {
  &:focus {
    outline: none;
  }
}

.accordion-item {
  margin-bottom: 30px;
}

.accordion-button {
  &:focus {
    box-shadow: none;
  }
}

.page-link {
  &:focus {
    box-shadow: none;
  }
}

section,
.section-t-space {
  padding-top: 40px;
}

:focus {
  outline: unset;
}

.table {
  > :not(:first-child) {
    border-top: none;
  }
}

.accordion-item {
  &:not(:first-of-type) {
    border-top: 1px solid rgba(0, 0, 0, 0.125);
  }
}

.accordion-button {
  &:not(.collapsed) {
    color: #212529;
    box-shadow: none !important;
  }
  &:focus {
    box-shadow: none !important;
  }
}

.accordion-body {
  padding: 0 1.25rem 1rem;
}

button:focus {
  &:not(:focus-visible) {
    outline: 0;
  }
}

.accordion-button {
  &:not(.collapsed) {
    background-color: transparent;
  }
}

.form-control {
  &:focus {
    box-shadow: none;
  }
}

form {
  label {
    margin-bottom: 8px;
  }
}

.form-control {
  height: 50px;
}

.media {
  display: flex;
}

[data-notify="progressbar"] {
  margin-bottom: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5px;
}

.progress-bar {
