.classic-effect {
  position: relative;
  overflow: hidden;
  &:before,
  &:after {
    background: var(--theme-deafult);
    width: 25%;
    position: absolute;
    content: "";
    opacity: 0;
    -webkit-transition: all 0.3s steps(4);
    transition: all 0.3s steps(4);
    z-index: 1;
    bottom: 100%;
    top: 0;
  }
  &:before {
    left: 0;
    transition-delay: 0s;
  }
  &:after {
    left: 25%;
    transition-delay: 0.025s;
  }
  .img-fluid {
    aspect-ratio: 1/1;
    object-fit: cover;
  }
  span {
    &:before,
    &:after {
      background: var(--theme-deafult);
      width: 25%;
      position: absolute;
      content: "";
      opacity: 0;
      -webkit-transition: all 0.3s steps(4);
      transition: all 0.3s steps(4);
      z-index: 1;
      bottom: 100%;
      top: 0;
    }
    &:before {
      left: 50%;
      transition-delay: 0.05s;
    }
    &:after {
      left: 75%;
      transition-delay: 0.15s;
    }
  }
  &:hover {
    &:before,
    &:after {
      bottom: 0;
      opacity: 0.5;
    }
    span {
      &:before,
      &:after {
        bottom: 0;
        opacity: 0.5;
      }
    }
  }
}

.basic-effect {
  position: relative;
  overflow: hidden;
  img {
    transition: all 0.5s ease;
  }
  &:after {
    position: absolute;
    content: "";
    background: linear-gradient(
      -180deg,
      rgba(var(--theme-gradient1), 0.8) 0%,
      rgba(var(--theme-gradient2), 0.8) 99%
    );
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    transition: all 0.5s ease;
  }
  &:hover {
    &:after {
      opacity: 1;
      transition: all 0.5s ease;
    }
    img {
      transform: scale(1.2) rotate(5deg);
      transition: all 0.5s ease;
    }
  }
}

