      height: 100vh;
      overflow: auto;
    }
    &.openSide {
      right: 0;
    }
  }
  &.top,
  &.bottom {
    height: 218px;
    width: 100%;
    .cart_media {
      display: flex;
      width: 100%;
      .cart_product {
        display: flex;
        width: 75%;
        overflow-y: auto;
        li {
          min-width: 245px;
          max-width: 225px;
          margin-right: 18px;
          padding-bottom: 0;
        }
      }
      .cart_total {
        width: 25%;
      }
    }
    .cart-inner {
      height: 218px;
      .cart_top {
        margin-bottom: 0;
        background-color: #fafafa;
        padding: 15px 44px;
        border-bottom: none;
      }
      .cart_media {
        padding: 0;
        .cart_product,
        .cart_total {
          padding: 30px 44px;
        }
      }
    }
  }
  &.top {
    top: -300px;
    &.openSide {
      top: 0;
    }
  }
  &.bottom {
    bottom: -300px;
    &.openSide {
      bottom: 0;
    }
  }
