.collection-collapse-block {
  padding-bottom: 24px;
  .collapse-block-title {
    position: relative;
    cursor: pointer;
    text-transform: uppercase;
    font-size: 16px;
    color: lighten($black, 26.5%);
    font-weight: 600;
    margin: 30px 0 0 0;
    &:after {
      position: absolute;
      display: block;
      top: 0;
      right: -3px;
      content: "\f0d7";
      font-family: $font-awesome;
      color: lighten($black, 26.5%);
    }
  }
  &.open {
    .collection-collapse-block-content {
      max-height: 500px;
      transition-property: all;
      transition-duration: 0.5s;
      transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
    }
  }
  .collection-collapse-block-content {
    max-height: 0;
    overflow: hidden;
    transition-property: all;
    transition-duration: 0.5s;
    transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
    .color-selector {
      margin-top: 30px;
    }
    .collection-brand-filter {
      max-height: 170px;
      overflow-y: auto;
      overflow-x: hidden;
      margin-top: 30px;
      .category-list {
        li {
          display: flex;
          font-size: 16px;
          text-transform: capitalize;
          line-height: 18px;
          a {
            color: $grey;
            padding-bottom: 10px;
          }
          &:last-child {
            a {
              padding-bottom: 0;
            }
          }
        }
      }
      .collection-filter-checkbox {
        margin-bottom: 12px;
        display: flex;
        align-items: center;
        &:last-child {
          margin-bottom: 0;
        }
        span {
          width: 14px;
          height: 14px;
          position: relative;
          border-radius: 50%;
          border: 1px solid #eee;
          top: 0;
          left: 0;
          margin-left: 10px;
        }
        label {
          text-transform: uppercase;
          cursor: pointer;
          font-size: 12px;
          color: lighten($black, 46.5%);
          font-weight: 600;
          letter-spacing: 0.5px;
          margin-left: 15px;
          &:before {
            top: 1px;
            height: 15px;
            width: 15px;
            background: $white;
            border: 1px solid lighten($black, 46.5%);
            box-shadow: none;
          }
          &:after {
            top: 1px;
            height: 15px;
            width: 15px;
            background-size: 65%;
          }
        }
        .custom-control-input {
          &:checked {
            ~ .custom-control-label {
              &:after {
                background-image: url("data:image/svg+xml;charset=utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'><path fill='%23000' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/></svg>");
              }
            }
          }
        }
      }
      &.color-filter {
        .collection-filter-checkbox {
          span {
            margin-left: 10px;
          }
          label {
            margin-left: 8px;
          }
        }
      }
    }
  }
}

.color-selector {
