// product page //
.product-slick,
.rtl-product-slick,
.product-right-slick,
.rtl-product-right-slick {
  .slick-list {
    overflow: visible;
  }
  .owl-prev,
  .owl-next {
    opacity: 0;
    transform: scale(1);
    transition: all 0.5s ease;
    margin-top: -10px;
    &:before {
      font: normal normal normal 14px/1 $font-awesome;
      opacity: 1;
      color: black;
      background-color: white;
      border-radius: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  .owl-next {
    right: 1px;
    &:before {
      content: "\f105";
    }
  }
  .owl-prev {
    left: 1px;
    z-index: 1;
    &:before {
      content: "\f104";
    }
  }
  .owl-item {
    > div {
      //display: flex;
      margin-bottom: 15px;
    }
  }
  &:hover {
    .owl-prev,
    .owl-next {
      opacity: 1;
      transform: scale(1.05);
      transition: all 0.5s ease;
      &:before {
        opacity: 1;
      }
    }
    .owl-next {
      right: 20px;
    }
    .owl-prev {
      left: 20px;
    }
  }
}

.owl-item {
  > div.owl-thumb {
    margin: 5px 5px 0px 0px;
    border: 1px solid $round-border;
    transition: all 0.5s ease;
    cursor: pointer;
