        margin-bottom: 8px;
        line-height: 23px;
      }
    }
  }
}

.dashboard {
  .box-head {
    h2 {
      font-size: 18px;
      margin: 20px 0 0;
      text-transform: capitalize;
      color: #333;
    }
  }
  .box {
    a {
      color: var(--theme-deafult);
