          text-transform: capitalize;
          margin-top: 10px;
        }
      }
      h5 {
        margin-bottom: 0;
        line-height: 1.2;
      }
    }
    .detail-part {
      .title-detail {
        background-color: #f7f8fa;
        text-align: center;
        padding: 8px 0;
        border-top: 1px solid #dbdbdb;
        border-bottom: 1px solid #dbdbdb;
        h5 {
          margin-bottom: 0;
          text-transform: uppercase;
        }
      }
      .inner-detail {
        padding: 15px;
        p {
          margin-bottom: 0;
