// typography page //
.typography_section {
  .typography-box {
    margin-bottom: 30px;
    p {
      line-height: 22px;
    }
    .headings {
      border-bottom: 1px solid #f1f1f1;
      margin-bottom: 20px;
      background-color: rgba($grey-about, 0.5);
      padding: 8px 12px;
      h3 {
        text-transform: capitalize;
        color: #2d2d2d;
        margin-bottom: 0;
        font-weight: 400;
        font-size: 20px;
      }
      span {
        color: $grey;
        code {
          color: $grey;
        }
      }
    }
    .typo-content {
      &.heading_content {
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
          font-weight: 400;
          color: black;
