/*=====================
24.Responsive CSS start
==========================*/

@media (min-width: 1430px) {
  .container {
    max-width: 1400px;
  }
}

@media (max-width: 1430px) {
  .left-header {
    .top-header {
      padding-left: 0;
      .header-contact {
        display: none;
      }
    }
    .sidenav {
      left: -300px;
      &.open-side {
        left: 0;
      }
      .sidebar-back {
        display: block;
        color: black;
        border-bottom: 1px solid #1a1b2d;
      }
      .brand-logo {
        display: none;
      }
      .left-sidebar_center {
        padding: 0 0 25px 25px;
      }
    }
    .main-menu {
      .menu-left {
        .navbar {
          display: block;
        }
        .mobile-logo {
          display: block;
          border: none;
        }
      }
    }
    .search-overlay {
      padding-left: 0;
    }
  }
  .left-sidebar_space {
    padding-left: 0;
  }
  .banner-slider .height-banner {
    height: unset;
  }
  .pet-parallax {
    .pet-decor {
      left: 60px;
      bottom: -125px;
      img {
        width: 240px;
      }
    }
  }
  .tools_slider {
    .home-slider {
      .slider-contain {
        margin-left: 80px;
      }
      .home {
        .tools-parts,
        .tools-parts1 {
          img {
            width: 250px;
          }
        }
        .tools-parts {
          right: 0;
        }
        .tools-parts1 {
          right: 10%;
        }
        #tools-move2 {
          right: 7%;
        }
      }
    }
  }
  // inner pages //
  .collection-product-wrapper {
    .product-top-filter {
      .product-filter-content {
        .search-count {
          width: 100%;
          border-left: 1px solid $round-border;
          border-bottom: 1px solid $round-border;
          text-align: center;
        }
        .collection-view {
          width: 20%;
          padding-right: 0 !important;
          border-left: 1px solid $round-border;
        }
        .collection-grid-view {
          padding: 20px !important;
          padding-left: 0 !important;
          width: 20%;
          border-left: 1px solid $round-border;
          ul {
            li {
              &:last-child {
                display: none;
              }
            }
          }
        }
        .product-page-filter {
          border-right: 1px solid $round-border;
          width: 30%;
        }
        .product-page-per-view {
          width: 30%;
        }
      }
      .popup-filter {
        .collection-view {
          width: 10%;
        }
        .product-page-per-view {
          width: 25%;
        }
      }
    }
  }
  .cart-section {
    .cart-buttons {
      > div {
        &:last-child {
          padding-right: 38px;
        }
      }
    }
    tbody {
      tr {
        td {
          min-width: 175px;
          .qty-box {
            .input-group {
              .form-control {
                width: 75px;
              }
            }
          }
        }
      }
    }
    tfoot {
      tr {
        td {
          padding-right: 41px;
        }
      }
    }
  }
  .product-right {
    .product-icon {
      .product-social {
        li {
          padding-right: 10px;
        }
      }
    }
  }
  .product-form-box {
    .timer {
      padding-left: 17px;
      span {
        width: 55px;
      }
    }
  }
  // rtl section //
  .rtl {
    .cart-section,
    .wishlist-section {
      .cart-buttons {
        > div {
          &:last-child {
            padding-left: 41px;
          }
        }
      }
    }
    .collection-product-wrapper {
      .product-filter-content {
        .collection-view {
          border-left: none;
          border-right: 1px solid $round-border;
        }
        .product-page-filter {
          border-left: 1px solid $round-border;
          border-right: none;
        }
      }
      .product-top-filter {
        .product-filter-content {
          .search-count {
            border-right: 1px solid $round-border;
            padding-right: 20px;
            h5 {
              text-align: center;
            }
          }
        }
      }
    }
    .product-right {
      .product-icon {
        .product-social {
          li {
            padding-left: 10px;
          }
        }
      }
    }
  }
  // dark //
  .dark {
    .collection-product-wrapper {
      .product-top-filter {
        .product-filter-content {
          .collection-view {
            border-color: $dark-border;
          }
          .product-page-filter {
            border-color: $dark-border;
          }
        }
      }
    }
  }
}

@media (max-width: 1430px) and (min-width: 1368px) {
  .banner-timer {
    .timer {
      margin-top: 45px;
    }
  }
}

@media (max-width: 1430px) and (min-width: 1200px) {
  .layout3-menu {
    max-width: 100%;
    .main-menu {
      .menu-left {
        .navbar {
          padding: 40px 10px 40px 0;
        }
      }
    }
    .pixelstrap {
      > a {
        padding-right: 38px;
